import React,{useState} from "react";
import './Trash.css'

function Trash(){
const[curr,state]=useState({
count:0
})

function Reset(event){
state(prevState=>{
    return{count:prevState.count=0}
})
}
function handleChange(event){
state(prevState=>{
    var val=event.target.value
    return{count:val}
})
}
function ADD(){
    state(prevState=>{
        var val=Number(prevState.count)
        return{count:val+1}
    })
}
    return(
        <div className="trash">
<h4>Hello Arrya Destructure {curr.count}</h4>
<button onClick={Reset}>Reset</button>
<button onClick={ADD}>Plus+1</button>

<input type="number" placeholder="Enter Integer.."
id="fname" onChange={handleChange}/>
        </div>
    )
}
export default Trash;